import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import React, { useLayoutEffect, useRef } from 'react'
import styled from 'styled-components'
import DrawSvg from '../DrawSvg';

const Section = styled.section`
min-height: 100vh;
width: 100vw;
background-color: ${(props) => props.theme.body};
position: relative;
display: inline-block;

`;
const Title = styled.h1`
  font-size: ${(props) => props.theme.fontxxl};
  text-transform: capitalize;
  color: ${(props) => props.theme.text};
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem auto;
  border-bottom: 2px solid ${(props) => props.theme.text};
  width: fit-content;

  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontxl};
  }
`;
const Container = styled.div`
width: 70%;
height: 200vh;
background-color: ${(props) => props.theme.body};
margin: 0 auto;
display: flex;
justify-content: center;
align-items: center;
position: relative;

@media (max-width: 64em) {
  width: 80%;
}
@media (max-width: 48em)  {
  width: 90%;
}
`;
const SvgContainer = styled.div`
display: flex;
justify-content: center;
align-items: center;
`;

const Items = styled.ul`
list-style:none;
width: 100%;
height: 100%;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
/* background-color: lightblue; */

@media (max-width: 48em){
  width: 90%;
}

&>*:nth-of-type(2n +1) {
  justify-content: start;
  @media (max-width: 48em) {
    justify-content:  center;
  }

  div{
    border-radius: 50px 0 50px 0;
    text-align: right;
    
    @media (max-width: 48em) {
      border-radius: 0 50px 0 50px; 
    text-align: left; 
      p {
        border-radius: 40px 0 40px 0; 

      } 
    }
  }
   p {
    border-radius: 40px 0 40px 0; 
   }
}
&>*:nth-of-type(2n){
    justify-content: end;
    @media (max-width: 48em) {
      justify-content:  center;
    }
      div{
        border-radius: 0 50px 0 50px; 
        text-align: left; 

      
      }
      p {
        border-radius: 0 40px 0 40px; 
      }
    }
    `;
const Item = styled.li`
width: 100%;
height: 100%;
display: flex;

@media (max-width: 48em){
  justify-content: flex-end !important;
 }
`;
const ItemContainer = styled.div`
width: 40%;
height: fit-content;
padding: 1rem;
border: 3px solid ${(props) => props.theme.text};

@media (max-width: 48em){
  width: 70%;

 }
`;

const Box = styled.p`
height: fit-content;
background-color: ${(props) => props.theme.carouselColor}
color: ${(props) => props.theme.text};
padding: 1rem;
position: relative;
border: 1px solid ${(props) => props.theme.text};
`;
const SubTitle = styled.span`
display: block;
font-size: ${(props) => props.theme.fontxl};
text-transform:capitalize;
color: ${(props) => props.theme.text};

@media (max-width: 40em) {
  font-size: ${(props) => props.theme.fontlg};
  font-weight: 600;
}
`;
const Text = styled.span`
display: block;
font-size: ${(props) => props.theme.fontsm};
text-transform:capitalize;
color: ${(props) => props.theme.text};

font-weight:400;
margin: 0.5rem 0;
@media (max-width: 40em) {
  font-size: ${(props) => props.theme.fontxs};
}
`;

const RoadMapItem = ({title, subtext,addToRef }) => {
  return(
    <Item ref={addToRef}>
      <ItemContainer>
        <Box>
          <SubTitle>{title} </SubTitle>
          <Text> {subtext}</Text>
        </Box>
      </ItemContainer>
    </Item>
  );  
};

const Roadmap = () => {
  const revealRefs = useRef([]);
  revealRefs.current = [];
  gsap.registerPlugin(ScrollTrigger);

const addToRefs = (el) => {
    if(el && !revealRefs.current.includes(el)) {
        revealRefs.current.push(el);
    }
};

useLayoutEffect(() => {
 let t1 = gsap.timeline();
revealRefs.current.forEach( (el, index) => {
t1.fromTo(
    el.childNodes[0],
    {
      y: "0",
    },
    {
      y:"-30%", 
      
      scrollTrigger: {
        id: `section-${index + 1}`,
        trigger: el,
        start: 'top center+=200px' ,
        end:'bottom center',
        scrub:true,
        // markers:true, 
      },
    }
  );
});

    return () => {
      if (t1) t1.kill();    
    };
 },  []);

    return (
      <Section id="roadmap">
          <Title>Roadmap</Title>
          <Container>
              <SvgContainer>
                <DrawSvg />
              </SvgContainer> 
              <Items>
              <Item>&nbsp;</Item>  
                <RoadMapItem  addToRef={addToRefs}  title="Phase 1 - 10% Sold " subtext="1 Eth giveaway to be raffled for the first 500 minters. We will market and leverage social media and other platforms to expand Winking Puppies community globally." />
                <RoadMapItem  addToRef={addToRefs}  title="Phase 2 - 20% Sold" subtext="The Winking Puppies DOA will be created and holders will be able to vote for the DAO's first blue chip NFT purchases." />
                <RoadMapItem  addToRef={addToRefs}  title="Phase 3 - 50% Sold" subtext="The Wingking Puppies (TWP) token will be launched alongside our staking portal. Holders will be able to claim their TWP token airdrop.  Special incentives for early stakers will be announced." />
                <RoadMapItem  addToRef={addToRefs}  title="Phase 4 - 60% Sold" subtext="We will create our own Metaverse and Winking Puppies holders will have access to our private events. We will purchase land in both Decentraland and Sandbox. Winking Puppies members will have exclusive access and early rights to land in the new Metaverse!" />
                <RoadMapItem  addToRef={addToRefs}  title="Phase 5 - 70% Sold" subtext="The Winking Puppies will purchase a bulk of blue chip NFTs and Metaverse items which will be fractionalized and distributed to our NFT holders.  Exclusive perks for all holders such as merch, giveaways, meetups, special collabs, discounts.  More details will be provided in our official Discord and social media pages." />
                <RoadMapItem  addToRef={addToRefs}  title="Phase 6 - 80% Sold" subtext="Winking Puppies are keen to given back to communities by helping protect the Winking Puppies in our world. We are happy to announce that $50,000 will be given in donations to dog shelter’s, NGOs, or non-profit organization to combat dog killing in China and other regions of the world. Winking Puppies will search for potential partners for our project and the community will vote which organization will be a good fit. We are building a community where all holders voice matters." />
                <RoadMapItem  addToRef={addToRefs}  title="Phase 7 - 90% Sold" subtext="The Winking Puppies holders will get an Exclusive Credit Card with your TWP NFT Avatar imprinted. Also, you will have access to merchandise for hoodies, shirts, hats (many more). Lastly, you will have access to our Digital Wear & Accessories Collection that is compatible with your Avatar in the metaverse." />
                <RoadMapItem  addToRef={addToRefs}  title="Phase 8 - 95% Sold" subtext="For every 2 Winking Puppies NFT's you own, you will get an opportunity to get a free NFT of our 2nd Generation Breeding Collection." />
                <RoadMapItem  addToRef={addToRefs}  title="Phase 9 - 100% Sold" subtext="TWP DAO will be set into full motion where the community decides on purchases and sells for our DAO's blue chip holdings. Profits of NFT flips will be distributed to all Winking Puppies holder through our TWP token. Our top holders will be provided a Dubai all-inclusive package with airfare for a week of unforgettable events.  Dubai Marina Yacht Tour, Race supercars around the Dubai Autodrome, meet and network with whales in the NFT & crypto space, and more.
" />
             
              </Items>   
          </Container>    
      </Section>
    )
}

export default Roadmap
